<template>
  <div id="akt">
    <img class="profilepic" v-if='user.sub' :src='user.picture'/>
    <Input @clicked='add'/>
    <List @clicked='handle' :list='list' :completed='completed'/>
  </div>
</template>

<script>
import Input from './Input'
import List from './List'
import axios from 'axios'

export default {
  name: "akt",
  data(){
    return {
      user: {},
      list: [],
      completed: []
    }
  },
  methods: {
    add(item){
      this.list.push({todo: item.item})
      axios.post("/api/addtodo", {item: item.item})
  
    },
    handle(obj){
      
      if(obj.val.index >= 0){
        
      var newItem = this.list.slice(obj.val.index, ++obj.val.index)
      console.log(newItem)
      this.list.splice(--obj.val.index, 1)
      this.list.unshift(newItem[0])
      }
      else if(obj.val.completed >= 0){
         this.completed.push(this.list[obj.val.completed])
        this.list.splice(obj.val.completed, 1)
      }
      else if(obj.val.delete >= 0){
        var id = this.list[obj.val.delete].todo_id
        axios.delete(`/api/delete/${id}`)
        this.list.splice(obj.val.delete, 1)
      }
    }
  },
  components: {
    Input, List
  },
  mounted: function(){
    axios.get('api/user').then(info => {
      this.user = info.data.user
      axios.get('/api/getitems').then(list => {
        this.list = list.data
      })
    })
    if (!user.sub){
      this.$router.push("/")
    }
  }
};
</script>

<style>
#akt {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  padding: 3vh 2vw;
  height: 90vh;
}
img.profilepic {
  width: 40px;
  border-radius: 20px;
  position: fixed;
  top: 5px;
  right: 5px;
}
</style>
