<template>
<div>
   
    <div v-for='(todo, i) in list' :key='i'> 

       <li class="todoitem"> 
           <input class='checkitem' name='check' type='checkbox' @click='star({completed: i})'/> 
           {{todo.todo}} 
           <div class='btncontainer'>
           <div class="btn-group btn-group-sm" role="group" aria-label="Basic example">
           <button class="btn btn-light" @click='star({index: i})'> <i class="far fa-star"/> </button>
           <button class="btn btn-light" @click='star({delete: i})'> <i class="far fa-trash-alt"/> </button>
           </div>
           </div>
       </li> 
       
        </div>  

       
   

    <br/>
    <button class="btn btn-light btn-sm" type="button" data-toggle="collapse" data-target="#collapseExample" aria-expanded="false" aria-controls="collapseExample"
    v-if='completed[0]'>
    Completed &thinsp;
    <span class="badge badge-primary">{{completed.length}}</span> 
  </button>
   <br/><br/>
    <div class="collapse" id="collapseExample">
        <div class='completed' v-for='(item, index) in completed' :key="index + '-complete'">
            <li >
                {{item.todo}}
             </li>
        </div>
    </div>
</div>
</template>

<script>

export default {
      
    props: ['list', 'completed'],
    methods: {
        star(val){
            if(val.completed >= 0){
               var tdarr = document.getElementsByName('check');
               tdarr[val.completed].checked=false;   
            }
            
            this.$emit('clicked', {val})
        }
    },
   
    
}

</script>

<style>
li {
    list-style: none;
    font-size: 1em;
    margin: 1vh 1vw;
    padding: 10px 5px;
    border-radius: 5px;
    background-color: white;
    display: flex;
    align-items: center;
    overflow: hidden;
}
div.completed {
    opacity: 0.5;
    margin: 1vh 1vw;
}
div.btncontainer {
    position: fixed;
    right: 6vw;
}
input.checkitem {
   margin-left: 5px;
   margin-right: 10px;
}

</style>